body.Session .Session-form {
  width: 500px;
  padding: 0 44px 0;
  background: #efefef;
  float: right;
  position: relative;
}
body.Session .Logo {
  margin-bottom: 44px;
  font-size: 24px;
  font-weight: 300;
}
body.Session .Session-version {
  font-family: 'Droid Sans Mono';
  font-size: 11px;
  right: 12px;
  top: 4px;
  position: fixed;
  z-index: 10;
}
body.Session .Session-field {
  width: 100%;
  margin: 0 0 12px;
}
body.Session .Session-error {
  margin: 0 0 4px;
  color: #ff5b57;
  font-weight: 600;
}
body.Session .Session-status {
  width: 100%;
  margin: 0;
  text-align: center;
  color: #00ac71;
  left: 0;
  top: 56px;
  position: absolute;
}
body.Session input {
  margin: 0 0 2px;
}
body.Session fieldset {
  margin: 0 0 34px;
}
body.Session button[type=submit] {
  margin: 0 0 18px;
}
body.Session p {
  margin: 0 0 44px;
}
body.Session .Session-footer {
  padding: 12px 0 0;
  border-top: solid 1px #cdcdcd;
  text-align: center;
}
@media (max-width: 1023px) {
  body.Session .Background {
    display: none;
  }
  body.Session .Session-form {
    width: auto;
    float: none;
  }
}
@media (max-width: 479px) {
  body.Session .Session-form {
    padding: 0 18px;
  }
}
body.Listing .Filters__filter--totals {
  padding-top: 6px;
  float: left;
}
body.Listing .Filters__filter--totals strong {
  font-weight: bold;
}
body.Listing .Filters__filter--form {
  float: right;
}
body.Listing .Filters__search,
body.Listing .Filters__perpage {
  margin-right: 8px;
}
body.Listing .Filters__search {
  width: 200px;
}
body.Listing .Filters__perpage {
  width: 70px;
}
body.Listing .Filters__label {
  margin-right: 4px;
  font-size: 12px;
}
body.Listing .Noresults {
  padding: 64px 0;
  text-align: center;
}
body.Listing .Noresults__title {
  font-size: 30px;
  font-weight: 300;
}
body.Listing .Noresults__link {
  margin: 0;
}
@media (max-width: 1023px) {
  body.Listing .Filters__search {
    width: 170px;
  }
}
@media (max-width: 767px) {
  body.Listing .Filters__filter--totals,
  body.Listing .Filters__filter--form {
    text-align: center;
    float: none;
  }
  body.Listing .Filters__filter--totals {
    margin: 0 0 18px;
  }
}
@media (max-width: 479px) {
  body.Listing .Filters__search,
  body.Listing .Filters__perpage {
    width: 100%;
    margin: 0 0 6px;
  }
  body.Listing .Filters__label {
    margin: 0 0 6px;
    display: inline-block;
  }
  body.Listing .Filters .Form__button {
    width: 100%;
  }
}
